@import '../base.scss';

.c-ingredients {
  text-align: center;
  max-width: 125rem;
  margin: auto;

  &__right {
    position: relative;
    padding: 2.375rem 4.5625rem;
    max-width: 48.75rem;

    &-icon {
      position: relative;

      svg {
        width: 100%;
        height: auto;
        display: block;
        // fill: $s-color--background-octonary;
      }
    }

    &-background {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__left {
    max-width: 60rem;
  }

  &__titles {
    display: none;

    &-pre,
    &-main {
      display: block;
    }
  }

  &__items {
    padding: 3.3125rem 0 0;
    align-items: center;
  }

  &__item {
    cursor: pointer;

    &-icon {
      padding: 0.8125rem;
      width: 2.9375rem;
      height: 2.9375rem;
      border: 1px solid $s-color--aqua;
      border-radius: 50%;
      margin: auto;

      svg,
      * {
        fill: $s-color--dark-blue;
        width: 100%;
        height: auto;
        display: block;
      }

      .is-large & {
        width: 5.5625rem;
        height: 5.5625rem;
        border-width: 5px;
        border-color: $s-color--light-blue;
        background: $s-color--aqua;
        padding: 1.25rem;

        svg,
        * {
          fill: $s-color--white;
        }
      }
    }

    &-title {
      display: block;
      padding-top: 1.4375rem;
      color: $s-color--grey-green;

      .is-large & {
        padding-top: 1.625rem;
        color: $s-color--blue-green;
      }
    }
  }

  &__content {
    padding: 2.625rem 2.5rem;

    &-item {
      display: none;

      &.is-active {
        display: block;
      }
    }

    &-title {
      display: block;
    }

    &-body {
      display: block;
      padding-top: 1.3125rem;
    }
  }

  @include t-media-query($s-small-up) {
    display: flex;
    align-items: center;
    //justify-content: space-between;
    text-align: left;

    &__left {
      order: 1;
      width: 50%;
      padding: 2.5rem;
    }

    &__content {
      padding: 0;
    }

    &__titles {
      display: block;

      &-pre {
        padding-bottom: 1.4375rem;
      }
    }

    &__items {
      padding: 3.125rem 0 3.375rem;
    }

    &__item {
      &-title {
        text-align: center;
      }


      &-icon {
        width: 3.5rem;
        height: 3.5rem;
        padding: 0.9375rem;
      }

      &:hover &-icon {
        border-color: $s-color--light-blue;
        background: $s-color--aqua;

        svg,
        * {
          fill: $s-color--white;
        }
      }

      .is-large &-icon {
        width: 3.5rem;
        height: 3.5rem;
        padding: 0.625rem;

        svg,
        * {
          fill: $s-color--dark-blue;
        }
      }

      &-title,
      .is-large &-title {
        padding-top: 1.6875rem;
      }
    }

    &__right {
      order: 2;
      width: 50%;
    }
  }

  @include t-media-query($s-medium-up) {
    &__left {
      padding: 8.75rem;
    }

    &__right {
      padding: 9.75rem 8.875rem;
    }
  }
}